html,
#root,
.main-container {
  background-color: #2c2f33;
  color: white;
}

.main-container {
  height: 95vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 10vh;
}

.items {
  margin: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.mute-button {
  position: fixed;
  top: 0;
  left: 0;
  margin: 20px;
}

code {
  margin-top: 5px;
}

.headline {
  margin: 70px 0;
}

.instructions,
.input-form {
  margin: 10px 0;
}

